.cost-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0,5rem;
	margin: 1rem;
	background-color: #bd8025;
	padding: 10px;
}

.cost-item__description {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	align-items: flex-end;
	flex-flow: column-reverse;
	justify-content: flex-start;
	flex: 1;
}

.cost-item h2 {
	color: #3a3a3a;
	font-size: 1rem;
	flex: 1;
	margin: 0 1rem;
	color: #fefae1;
}

.cost-item__price {
	font-size: 1rem;
	font-weight: bold;
	color: #fefae1;
	background-color: #2a5b84;
	border: 1px solid white;
	padding: 0.5rem;
	border-radius: 12px;
}

@media (min-width:580px){
	.cost-item_description{
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		flex: 1;
	}

	.cost-item_description {
		font-size: 1.25rem;
	}
}